.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 200px;
  height: 200px;
  -webkit-box-shadow: 0 6px 6px #999, 0 6px 6px #999;
          box-shadow: 0 6px 6px #999, 0 6px 6px #999;
  text-align: left;
}

.card > .img-container {
/*  height: 60%;
*//*  overflow: hidden;
*/  text-align: center;
/*  background:#6CADDC;
*/}
.card > .img-container > img {
  width: 200px;
  height: 200px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}



